.App {
  text-align: center;
  height: 100%;
  font-family: 'Open Sans';
}

.dark {
  background: #000;
  color: #fff;
}

.view {
  padding: 84px 20px 20px;
}

.view h1, .view h2 {
  font-weight: normal;
  font-size: 1.1rem;
  margin-top: 0;
  margin: 0 0 5px 0;
  color: var(--primary-gold);
}

.view h2 {
  font-size: 0.8rem;
  color: #fff;
  margin: 0 0 15px 0;
}

/* variables */
:root {
  --primary-gold: #faaa5f;
  --primary-red: #350202;
}
